import { create, type StateCreator } from "zustand";
import { immer } from "zustand/middleware/immer";
import getDefaultNodeData from "./utils/getDefaultNodeData";
import type { Variables, VariableCategory } from "./types";
import { type ConfigId } from "@/constants/ConfigData";
import { axiosGet, axiosPost } from "@/services/axiosService";

import {
  applyNodeChanges,
  applyEdgeChanges,
  addEdge,
  updateEdge,
  getIncomers,
  type Node,
  type Edge,
  type XYPosition,
  type OnNodesChange,
  type OnEdgesChange,
  type OnConnect,
  type OnEdgeUpdateFunc,
} from "@xyflow/react";

interface State {
  nodes: Array<Node<Record<string, any>>>;
  edges: Array<Edge>;
  metaData: Record<string, any>;
  selectedNodeId: string;
  nextNodeId: number;
  variables: Variables;
  openConfigModalId: ConfigId | "";
  segmentVars: Array<any>;
  isSelPanelSCollapse: boolean;
  isWhatsappLoading: boolean;
  nodeErrors: Record<string, Array<string>>;
  leadsData: Array<Record<string, any>>;
  leadsSaveTimer: NodeJS.Timeout | null;
  edgeHoverId: string | null;
}

interface Action {
  handleNodesChange: OnNodesChange;
  handleEdgesChange: OnEdgesChange;
  handleEdgesUpdate: OnEdgeUpdateFunc;
  addNode: (type: string, position: XYPosition) => void;
  removeNode: (id: string) => void;
  duplicateNode: (copyId: string) => void;
  selectNode: (id: string) => void;
  addEdge: OnConnect;
  removeEdge: (id: string) => void;
  setMetaData: (data: Record<string, any>) => void;
  setVariables: (node: Node) => void;
  setOpenConfigModalId: (configId: ConfigId | "") => void;
  fetchSegmentData: (listId: string) => void;
  updateLeadsData: (leadId: string, data: Record<string, any>) => void;
  setIsSelPanelSCollapse: (value: boolean) => void;
  setIsWhatsappLoading: (value: boolean) => void;
  setNodeErrors: (nodeErrors: Record<string, Array<string>>) => void;
  setCampaignData: (data: Record<string, any>) => void;
  setEdgeHoverId: (id: string | null) => void;
  setNodes: (nodes: Array<Node<Record<string, any>>>) => void;
}

const stateStore: StateCreator<State> = () => ({
  nodes: [],
  edges: [],
  metaData: {},
  selectedNodeId: "",
  nextNodeId: 1,
  variables: {},
  openConfigModalId: "",
  segmentVars: [],
  isSelPanelSCollapse: true,
  isWhatsappLoading: false,
  nodeErrors: {},
  leadsData: [],
  leadsSaveTimer: null,
  edgeHoverId: null,
});

const actionStore: StateCreator<
  State & Action,
  [["zustand/immer", never]],
  [],
  Action
> = (set, get) => ({
  handleNodesChange: (changes) =>
    set((state) => {
      state.nodes = applyNodeChanges(changes, state.nodes);
    }),
  handleEdgesChange: (changes) => {
    set((state) => {
      state.edges = applyEdgeChanges(changes, state.edges);
    });
  },
  handleEdgesUpdate: (oldEdge, newConnection) => {
    set((state) => {
      state.edges = updateEdge(oldEdge, newConnection, state.edges);
    });
  },
  addNode: (type, position) => {
    set((state) => {
      const id = String(state.nextNodeId);

      state.nodes.push({
        id,
        type,
        position,
        data: getDefaultNodeData(id, type),
      });
      state.nextNodeId += 1;
    });
  },
  removeNode: (id) => {
    set((state) => {
      state.nodes = state.nodes.filter((node) => node.id !== id);
      state.edges = state.edges.filter(
        (edge) => edge.source !== id && edge.target !== id,
      );

      state.selectedNodeId = "";
    });
  },
  duplicateNode: (copyId) => {
    set((state) => {
      const node = state.nodes.find((node) => node.id === copyId);
      if (!node) return;

      const id = String(state.nextNodeId);
      const { type, position, data } = node;

      state.nodes.push({
        id,
        type,
        position: {
          x: position.x,
          y: position.y + 150,
        },
        data,
      });
      state.nextNodeId += 1;
    });
  },
  selectNode: (id) => {
    set((state) => {
      state.selectedNodeId = id;
    });
  },
  addEdge: (connection) => {
    set((state) => {
      const edge = {
        ...connection,
        type: "custom",
        updatable: "target",
      };

      state.edges = addEdge(edge, state.edges);
    });
  },
  removeEdge: (id) => {
    set((state) => {
      state.edges = state.edges.filter((edge) => edge.id !== id);
    });
  },
  setMetaData: (data) => {
    set((state) => {
      Object.assign(state.metaData, data);
    });
  },
  setVariables: (node) => {
    set((state) => {
      state.variables = findVariables(node, state.nodes, state.edges);
    });
  },
  setOpenConfigModalId: (configId) => {
    set((state) => {
      state.openConfigModalId = configId;
    });
  },
  fetchSegmentData: async (listId) => {
    const tagPromise = axiosGet(`/audience/tags?list_id=${listId}`);
    const leadPromise = axiosGet(
      `/audience/leads?list_id=${listId}&type=upload`,
    );

    const [tagResponse, leadResponse] = await Promise.all([
      tagPromise,
      leadPromise,
    ]);

    set((state) => {
      state.segmentVars = tagResponse.data.data;
      state.leadsData = leadResponse.data.data;
    });
  },
  updateLeadsData: async (leadId, data) => {
    set((state) => {
      for (const lead of state.leadsData) {
        if (lead._id === leadId) {
          Object.assign(lead, data);
          break;
        }
      }

      if (state.leadsSaveTimer) clearTimeout(state.leadsSaveTimer);
      state.leadsSaveTimer = setTimeout(() => {
        axiosPost("/audience/editLeads", get().leadsData);
      }, 3000);
    });
  },
  setIsSelPanelSCollapse: (value) => {
    set((state) => {
      state.isSelPanelSCollapse = value;
    });
  },
  setIsWhatsappLoading: (value) => {
    set((state) => {
      state.isWhatsappLoading = value;
    });
  },
  setNodeErrors: (nodeErrors) => {
    set((state) => {
      state.nodeErrors = nodeErrors;
    });
  },
  setCampaignData: (data) => {
    set((state) => {
      Object.assign(state.nodes[0].data.audienceData, data);
    });
  },
  setEdgeHoverId: (id) => {
    set((state) => {
      state.edgeHoverId = id;
    });
  },
  setNodes: (nodes) => {
    set((state) => {
      state.nodes = nodes;
    });
  },
});

const useStore = create<State & Action>()(
  immer((...params) => ({
    ...stateStore(...params),
    ...actionStore(...params),
  })),
);

const initialState = useStore.getState();
export const useResetStore = () => useStore.setState(initialState, true);

export default useStore;

function findVariables(
  node: Node,
  nodes: Array<Node>,
  edges: Array<Edge>,
): Variables {
  const queue: Array<Node> = [node];
  const visited: Set<string> = new Set();
  const variables: Variables = {};

  while (queue.length) {
    const curNode = queue.shift();
    if (!curNode) break;

    for (const incomer of getIncomers(curNode, nodes, edges)) {
      if (!incomer.type || visited.has(incomer.id)) continue;

      const variableData = getVariableData(
        incomer.id,
        incomer.type,
        incomer.data,
      );

      for (const name of variableData.names) {
        const category = variableData.category ?? "node";

        if (!variables[category]) variables[category] = [];
        variables[category]?.push({
          id: `${incomer.id}_${name}`,
          name,
          type: "string",
          originNode: {
            id: incomer.id,
            type: incomer.type,
          },
        });
      }

      queue.push(incomer);
      visited.add(incomer.id);
    }
  }

  return variables;
}

export function getVariableData(
  id: string,
  type: string,
  data: any,
): {
  names: Array<string>;
  category?: VariableCategory;
} {
  switch (type) {
    case "campaign": {
      const campaignType = useStore.getState().metaData.campaignType;

      if (campaignType === "socialData") {
        return {
          names: data.webhookData.variables.map((item) => item.customName),
          category: "webhook",
        };
      }

      if (campaignType === "segment") {
        const segmentVars = useStore.getState().segmentVars;

        return {
          names: segmentVars.map((item) => item.name),
          category: "audience",
        };
      }

      return {
        names: [],
      };
    }
    case "ask_question":
    case "email":
    case "phone":
    case "username":
    case "address":
    case "result":
    case "assignees": {
      return {
        names: [data.variableName],
      };
    }
    case "button": {
      return {
        names: [data.variableName, data.buttonDetails.variableName],
      };
    }
    case "sendEmail": {
      return {
        names: [
          `${id}_email_status`,
          `${id}_email_opened`,
          `${id}_email_replied`,
        ],
        category: "email",
      };
    }
    case "hitApi": {
      return {
        names: data.hitApiDetails.variables.map(
          (variable) => variable.customName,
        ),
        category: "api",
      };
    }
    case "linkedIn": {
      const visitVariables = [
        `${id}_firstName`,
        `${id}_lastName`,
        `${id}_location`,
        `${id}_headline`,
        `${id}_industry`,
        `${id}_summary`,
      ];

      return {
        names: [`${id}_profileId`].concat(
          data.linkedInData?.nodeType === "visit" ? visitVariables : [],
        ),
        category: "linkedIn",
      };
    }
    default: {
      return {
        names: [],
      };
    }
  }
}
